<script>
import axios from 'axios'
export default {
    data() {
        return {
            intro: '',
            country: '',
            lastName: '',
            firstName: '',
            favoriteSalad: '',
            members: null,
            memberID: '',
            meats: ["chicken"],
            rices: ["chicken"],
            veggies: ["chicken"],
            counter: 0
        }
    },
    created() {
        this.getMembers();
    },
    methods: {
        updateMember(member){
            this.firstName = member.firstName;
            this.lastName = member.lastName;
            this.country = member.country;
            this.intro = member.intro;
            this.favoriteSalad = member.favoriteSalad;
            this.memberID = member._id;
            document.location = "#fname"
        },
        async upload(){
            console.log("members",this.members);
            for(let i = 0; i < this.members.length; i++){
                if(this.members[i]._id == this.memberID){
                    this.editMember(this.members[i])
                    return;
                }
            }

        try{
            if (this.firstName == ""|| this.lastName == "" || this.country == "" || this.intro == "" || this.favoriteSalad == "")
            return alert("Please fill in all data.")
            let r2 = await axios.post('/api/members', {
            firstName: this.firstName,
            lastName: this.lastName,
            country: this.country,
            intro: this.intro,
            favoriteSalad: this.favoriteSalad
        });
        this.addMember = r2.data;
        this.getMembers();
        }
        catch(error){
            console.log(error)
        }
        },
        async getMembers(){
            try {
            let response = await axios.get("/api/members");
            this.members = response.data;
            console.log(this.members);
            return true;
        } catch (error) {
            //console.log(error);
        }
        },
         async deleteMember(member) {
      try {
        await axios.delete("/api/members/" + member._id);
        this.getMembers();
        return true;
      } catch (error) {
        //console.log(error);
      }
    },
     async editMember(member) {
      try {
          if (this.firstName == ""|| this.lastName == "" || this.country == "" || this.intro == "" || this.favoriteSalad == "")
            return alert("Please fill in all data.")
        let r2 = await axios.put('/api/members/' + member._id, {
            firstName: this.firstName,
            lastName: this.lastName,
            country: this.country,
            intro: this.intro,
            favoriteSalad: this.favoriteSalad
        });
        this.addItem = r2.data;
        document.location = "/members";
        //console.log(this.addItem);
      } catch (error) {
        console.log(error);
      }
    },

    }
}
</script>